.app {
  padding: 0;
  display: grid;
  grid-template-rows: auto 1fr;
  height: 100vh;
}

.page {
  padding: 10px;
  max-width: 1000px;
  flex-grow: 1;
}

.page-container {
  display: flex;
  overflow: scroll;
  justify-content: center;
}

.settings-page {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  // flex-;
}

.settings-page * {
  font-size: 20px;
  overflow-wrap: break-word;
}

img {
  width: 100%;
}

.question-answers {
  display: grid;
  grid-auto-flow: row;
  grid-template-columns: repeat(3, 1fr);
  justify-content: stretch;
  align-items: stretch;
  flex-wrap: wrap;
  // margin: 2.5px;
}

.answer-text {
  width: 15em;
  text-align: center;
}

.answer {
  margin: 2.5px;
  height: 6em;
  display: flex;
  align-items: center;
  justify-content: center;
}

sub {
  line-height: 0;
}

h1 {
  text-align: center;
  font-size: 2em;
  margin: 10px 0;
}
/* tabs */
.tab-bar {
  display: flex;
  align-items: center;
  justify-content: stretch;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

button {
  --bg: black;
  $hover-anim-time: 0.2s;
  position: relative;
  padding: 10px;
  box-shadow: none;
  color: var(--bg);

  &,
  &::after,
  &::before {
    border-radius: 20px;
    display: block;
    transition: all $hover-anim-time ease-in-out;
    box-sizing: border-box;
  }

  &::before {
    content: "";
    position: absolute;

    border: 4px solid var(--bg);

    width: 100%;
    height: 100%;

    top: 0;
    left: 0;
  }

  &::after {
    content: "";
    position: absolute;
    background-color: var(--bg);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    opacity: 0;
    z-index: -1;
    transform: scale(0);
  }

  &:not(:hover)::after {
    // border-radius: 100%;
  }

  &:hover,
  &:focus-visible {
    color: white;

    &::after {
      opacity: 1;
      transform: scale(1);
    }

    &::before {
      opacity: 0;
    }
  }

  &:active:hover,
  &:focus-visible,
  &[aria-pressed="true"] {
    transform: scale(0.95);
  }
}

button.tab {
  border-radius: 0;
  flex-grow: 1;
  height: 3em;
}

.past-question-history {
  display: flex;
  flex-direction: column;
  gap: 10px;
  // padding: 10px;
}

.answered-question-bar {
  width: 100%;
  display: grid;
  gap: 5px;
  grid-template-columns: auto auto 1fr auto;
  align-items: center;
  justify-content: space-between;
}

.icon {
  width: 1em;
}

.upside-down {
  transform: rotate(-180deg);
}

.question-icon.right {
  color: darkgreen;
}

.question-icon.wrong {
  color: darkred;
}

.expand-icon {
  grid-column: 4;
  transition: all 200ms ease-in-out;
}

@media (max-width: 700px) {
  .question-answers {
    grid-template-columns: repeat(2, auto);
  }

  .answer-text {
    width: 10em;
    max-width: 35vw;
  }
}
